CAAA,CACA,cAAA,CACA,kBAAA,CACA,uCCTZ,CDWY,kFACE,wBCTd,CDWc,uFACE,UCThB,CDYc,wFACE,wBCVhB,CDac,2FACE,oCAAA,CACA,kBCXhB,CDeY,iFACE,4BAAA,CACA,aCbd,CDec,uFACE,aCbhB,CDmBQ,yBAjaF,sDAkaI,SAAA,CACA,UAAA,CACA,OAAA,CACA,SChBR,CDkBQ,gEACE,qBChBV,CACF","sourcesContent":["@forward \"./vImageMarkup.scss\";\n\n#kanopi-sidebar-container {\n  aside.left {\n    left: 15px;\n  }\n}\n\naside#kanopi-sidebar {\n  bottom: 20px;\n  display: block;\n  font-family: 'Helvetica', 'Arial', 'sans-serif';\n  left: auto;\n  position: fixed;\n  right: 30px;\n  background-color: transparent;\n  top: auto;\n  width: 60px;\n  z-index: 10000;\n\n  $white: #fff;\n  $gray: #f0f0f0;\n  $m-gray: #d3d3d3;\n  $d-gray: #adadad;\n  $black: #3a3a3a;\n  $green: #29d782;\n  $red: #f00;\n  $b-radius: 4px;\n\n  // Reset styles for sidebar only\n  div, span, object, iframe,\n  h1, h2, h3, h4, h5, h6, p, blockquote, pre,\n  abbr, address, cite, code,\n  del, dfn, em, img, ins, kbd, q, samp,\n  small, strong, sub, sup, var,\n  b, i,\n  dl, dt, dd, ol, ul, li,\n  fieldset, form, label, legend,\n  table, caption, tbody, tfoot, thead, tr, th, td,\n  article, aside, canvas, details, figcaption, figure,\n  footer, header, hgroup, menu, nav, section, summary,\n  time, mark, audio, video {\n    background: transparent;\n    color: $black;\n    border: 0;\n    font-family: 'Helvetica', 'Arial', 'sans-serif';\n    font-size: 100%;\n    font-weight: normal;\n    line-height: 1;\n    margin: 0;\n    outline: 0;\n    padding: 0;\n    text-align: left;\n    text-transform: none;\n    vertical-align: baseline;\n  }\n\n  article, aside, details, figcaption, figure,\n  footer, header, hgroup, menu, nav, section {\n    display: block;\n  }\n\n  nav ul {\n    list-style: none;\n  }\n\n  blockquote, q {\n    quotes: none;\n  }\n\n  blockquote:before, blockquote:after,\n  q:before, q:after {\n    content: '';\n    content: none;\n  }\n\n  a {\n    margin: 0;\n    padding: 0;\n    font-size: 100%;\n    vertical-align: baseline;\n    background: transparent;\n  }\n\n  ins {\n    background-color: $gray;\n    color: $black;\n    text-decoration: none;\n  }\n\n  mark {\n    background-color: $gray;\n    color: $black;\n    font-style: italic;\n    font-weight: bold;\n  }\n\n  del {\n    text-decoration: line-through;\n  }\n\n  abbr[title], dfn[title] {\n    border-bottom: 1px dotted;\n    cursor: help;\n  }\n\n  table {\n    border-collapse: collapse;\n    border-spacing: 0;\n  }\n\n  hr {\n    display: block;\n    height: 1px;\n    border: 0;\n    border-top: 1px solid $m-gray;\n    margin: 1em 0;\n    padding: 0;\n  }\n\n  input, select {\n    vertical-align: middle;\n  }\n\n  small {\n    font-size: 12px;\n    display: block;\n  }\n\n  h3 {\n    font-size: 1rem;\n  }\n\n  @function tint($color, $percentage) {\n    @return mix($black, $color, $percentage);\n  }\n  @mixin flex-center() {\n    display: flex;\n    align-items: center;\n  }\n  @mixin flex-between-center() {\n    @include flex-center();\n    justify-content: space-between;\n  }\n  @mixin loading {\n    border: 1px solid rgba(0, 153, 0, .3);\n    border-radius: 50%;\n    border-top-color: $green;\n    animation: spin 1s ease-in-out infinite;\n    -webkit-animation: spin 1s ease-in-out infinite;\n  }\n\n  @mixin check-radio-btns() {\n    display: inline;\n    position: relative;\n    background-color: transparent;\n    input {\n      opacity: 0;\n      position: absolute;\n      top: 0;\n      left: 0;\n      width: 100%;\n      height: 100%;\n\n      &:hover {\n        cursor: pointer;\n\n        + span {\n          background-color: rgba($green, .5);\n          color: rgba($black, .75);\n\n          * {\n            color: rgba($black, .75);\n          }\n        }\n      }\n\n      + span {\n        align-items: center;\n        background-color: $gray;\n        border-radius: $b-radius;\n        color: $d-gray;\n        display: flex;\n        flex-direction: column;\n        line-height: 1;\n        padding: 1rem;\n        position: relative;\n        pointer-events: none;\n        text-align: center;\n        transition: color 1s, background-color 1s;\n\n        * {\n          color: $d-gray;\n          transition: color 1s;\n        }\n      }\n\n      &:checked + span {\n        background-color: $green;\n        color: $black;\n        transition: color 1s, background-color 1s;\n\n        * {\n          color: $black;\n          transition: color 1s;\n        }\n      }\n    }\n  }\n\n  @keyframes spin {\n    to {\n      -webkit-transform: rotate(360deg);\n    }\n  }\n  @-webkit-keyframes spin {\n    to {\n      -webkit-transform: rotate(360deg);\n    }\n  }\n\n  > div {\n    padding: .25rem .5rem;\n\n    button {\n      border: 0;\n      cursor: pointer;\n      outline: none;\n      font-size: 14px;\n      background: transparent;\n    }\n\n    > button {\n      background-color: #f0f0f0;\n      border-radius: 50%;\n      display: block;\n      height: 45px;\n      padding: 0;\n      position: relative;\n      width: 45px;\n      overflow: hidden;\n      display: flex;\n      align-items: center;\n      justify-content: center;\n\n      svg {\n        border: 4px solid $gray;\n        border-radius: 50%;\n        overflow: hidden;\n        width: 100%;\n\n        .cls-1 {\n          transition: fill 1s;\n        }\n      }\n\n      &:hover {\n        svg {\n          .cls-1 {\n            fill: $m-gray;\n          }\n        }\n      }\n\n      &.loading {\n        &::after {\n          content: \"\";\n          width: 100%;\n          height: 100%;\n          position: absolute;\n          top: 0;\n          left: 0;\n          @include loading;\n        }\n      }\n    }\n\n    > div {\n      background-color: rgba(0, 0, 0, 0.45);\n      bottom: 0;\n      height: 100%;\n      left: 0;\n      overflow-y: auto;\n      position: fixed;\n      right: 0;\n      top: 0;\n      width: 100%;\n      z-index: 1005;\n\n      > article {\n        background-color: $white;\n        border-radius: $b-radius;\n        bottom: 0;\n        display: flex;\n        flex-direction: column;\n        height: auto;\n        left: 33%;\n        max-width: none;\n        overflow: visible;\n        padding: 1.5rem 1.5rem 1rem;\n        position: relative;\n        right: 33%;\n        top: 10%;\n        justify-content: space-between;\n        width: 33%;\n\n        header {\n          @include flex-between-center();\n          margin-bottom: 1rem;\n\n          > div {\n            @include flex-center();\n\n            svg {\n              height: 18px;\n            }\n\n            h3 {\n              color: $black;\n              font-family: 'Helvetica', 'Arial', 'sans-serif';\n              margin-left: .5rem;\n            }\n          }\n\n          > button {\n            color: $m-gray;\n            display: flex;\n            transition: color 1s;\n\n            svg {\n              width: 14px;\n              margin-right: 0.5rem;\n            }\n\n            span {\n              color: currentColor;\n            }\n\n            &:hover {\n              color: $black;\n            }\n          }\n\n          span.spinner {\n            @include loading;\n            display: inline-block;\n            height: 15px;\n            width: 15px;\n          }\n        }\n\n        > div {\n          position: relative;\n\n          > section.ticket-types {\n            display: grid;\n            grid-template-rows: 1fr 1fr;\n            grid-template-columns: 1fr 1fr;\n            gap: 1rem;\n            margin: 1.75rem 0 1rem 0;\n\n            label {\n              @include check-radio-btns();\n\n              input {\n                +\n                span {\n                  height: 100%;\n                  justify-content: center;\n                  padding: 0;\n                  width: 100%;\n\n                  div {\n                    padding: 2rem 0;\n                  }\n\n                  p {\n                    font-size: 14px;\n                    line-height: 1.15;\n                    margin-top: 5px;\n                    padding: 0 3rem 1.5rem;\n                  }\n\n                  svg {\n                    fill: currentColor;\n                    height: 35px;\n                    margin: 2rem;\n                  }\n\n                  h3, p {\n                    color: currentColor;\n                    text-align: center;\n                  }\n                }\n              }\n            }\n          }\n\n          > p {\n            color: $d-gray;\n            font-size: 14px;\n            margin-bottom: 1rem;\n          }\n        }\n\n        > section.options {\n          display: grid;\n          grid-gap: 1rem;\n          grid-template-columns: repeat(3, 1fr);\n        }\n\n        > section {\n          > small.error {\n            color: $red;\n            margin-bottom: 1rem;\n          }\n        }\n\n        .field {\n          select,\n          textarea {\n            background-color: transparent;\n            border: 1px solid $m-gray;\n            border-radius: $b-radius;\n            box-sizing: border-box;\n            color: $d-gray;\n            display: block;\n            font-family: 'Helvetica', 'Arial', 'sans-serif';\n            font-size: 1rem;\n            font-weight: 500;\n            margin-bottom: 1rem;\n            padding: 1rem;\n            transition: border 1s;\n            resize: vertical;\n            width: 100%;\n            -webkit-box-sizing: border-box;\n            -moz-box-sizing: border-box;\n\n            &::placeholder {\n              color: $m-gray;\n            }\n          }\n\n          label {\n            color: $d-gray;\n            font-size: 13px;\n          }\n\n          select {\n            -webkit-appearance: none;\n            -moz-appearance: none;\n            background-image: none;\n            background-image: url('https://d30y9cdsu7xlg0.cloudfront.net/png/221782-200.png');\n            background-position: calc(100% - 5px) center;\n            background-repeat: no-repeat;\n            background-size: 15px;\n            color: $d-gray;\n            font-size: 14px;\n            height: 40px;\n            padding: .5rem;\n\n            &::-ms-expand {\n              display: none;\n            }\n          }\n\n          &.error {\n            textarea {\n              border: 1px solid $red;\n              margin-bottom: .5rem;\n            }\n\n            select {\n              border: 1px solid #f00;\n              margin-bottom: 0.5rem;\n            }\n          }\n\n          #dz {\n            @include flex-center();\n            // align-items: center;\n            bottom: 20px;\n            // display: flex;\n            flex-flow: wrap;\n            position: absolute;\n            left: 5px;\n            // padding-left: 25px;\n            min-height: 25px;\n\n            > div.dz-message {\n              @include flex-center();\n              // align-items: center;\n              cursor: pointer;\n              // display: flex;\n              left: 0;\n              position: absolute;\n              top: calc(50% - 12.5px);\n              width: 200px;\n\n              svg {\n                height: 25px;\n                width: 20px;\n\n                path {\n                  transition: fill 1s;\n                  fill: $d-gray\n                }\n              }\n\n              span {\n                margin-left: 5px;\n                font-size: 12px;\n                color: $d-gray;\n                transition: color 1s;\n              }\n\n              &:hover {\n                svg {\n                  path {\n                    fill: $black;\n                  }\n                }\n\n                span {\n                  color: $black;\n                }\n              }\n            }\n\n            > div.dz-preview {\n              background-color: $gray;\n              position: relative;\n              padding: 2.5px 5px;\n              border-radius: 5px;\n              border: 1px solid $gray;\n              margin-right: 5px;\n              margin-top: 2.5px;\n              transition: padding .5s;\n\n              .dz-details {\n                display: flex;\n                align-items: center;\n\n                a {\n                  line-height: 0;\n                  margin-left: 5px;\n                  color: $d-gray;\n                }\n              }\n\n              .dz-filename {\n                color: #878383;\n                font-size: 12px;\n                line-height: 1;\n                position: relative;\n              }\n\n              .dz-remove {\n                display: block;\n                font-size: 0;\n                height: 100%;\n                position: absolute;\n                top: 0;\n                left: 0;\n                width: 100%;\n\n                &:after {\n                  content: \"\\02715\";\n                  font-size: 10px;\n                  color: $d-gray;\n                  position: absolute;\n                  top: 2px;\n                  transition: color 1s;\n                  right: 6px;\n                  opacity: 0;\n                  transition: opacity .5s;\n                }\n              }\n\n              &:hover {\n                .dz-remove {\n                  &:after {\n                    opacity: 1;\n                  }\n                }\n              }\n            }\n          }\n        }\n\n        > small.error {\n          color: $red;\n          margin-bottom: 1rem;\n        }\n\n        > div.alert {\n          @include flex-between-center();\n          background-color: transparentize($green, .85);\n          border-radius: $b-radius;\n          padding: .5rem .75rem;\n\n          aside {\n            @include flex-center();\n            color: $d-gray;\n            stroke: $d-gray;\n\n            small {\n              margin-left: .5rem;\n            }\n          }\n\n          nav {\n            button {\n              @include flex-center();\n\n              svg {\n                stroke: $green;\n                transition: stroke 1s;\n              }\n\n              span {\n                color: $green;\n                font-size: 12px;\n                font-weight: 700;\n                margin-left: .5rem;\n                transition: color 1s;\n              }\n\n              &:hover {\n                svg {\n                  stroke: tint($green, 50%);\n                }\n\n                span {\n                  color: tint($green, 50%);\n                }\n              }\n\n              &:disabled {\n                cursor: not-allowed;\n\n                svg {\n                  stroke: transparentize($green, .5);\n                }\n\n                span {\n                  color: transparentize($green, .5);\n                }\n              }\n            }\n          }\n\n          &.error {\n            background-color: transparentize($red, .75);\n            justify-content: flex-start;\n            gap: 1rem;\n\n            > span {\n              color: $red;\n              font-size: 1.65rem;\n            }\n          }\n        }\n\n        > footer {\n          display: flex;\n          position: relative;\n          justify-content: flex-start;\n          margin-top: 1rem;\n          left: 0;\n\n          button {\n            border-radius: 15px;\n            font-family: 'Helvetica', 'Arial', 'sans-serif';\n            min-width: 90px;\n            padding: .5rem 1rem;\n            transition: background-color 1s, color 1s;\n\n            &:first-of-type {\n              background-color: $green;\n\n              span {\n                color: $white;\n              }\n\n              &:hover {\n                background-color: tint($green, 50%);\n              }\n\n              &:disabled {\n                background-color: transparentize($green, .5);\n                cursor: not-allowed;\n              }\n            }\n\n            &:last-of-type {\n              background-color: transparent;\n              color: $m-gray;\n\n              &:hover {\n                color: $black;\n              }\n            }\n          }\n        }\n\n        @media (max-width: 764px) {\n          left: 2.5%;\n          right: auto;\n          top: 10%;\n          width: 95%;\n\n          > div.alert {\n            flex-direction: column;\n          }\n        }\n      }\n    }\n  }\n}","@charset \"UTF-8\";\n#kanopi-sidebar #v-image-markup {\n  z-index: 20000;\n}\n\n#kanopi-sidebar #v-image-markup .markup-container {\n  position: relative;\n}\n\n#kanopi-sidebar #v-image-markup .markup-container .tool-bar {\n  position: absolute;\n  width: 75px;\n  height: 100vh;\n  display: flex;\n  z-index: 2;\n  flex-direction: column;\n  top: 0;\n  right: 20px;\n  justify-content: center;\n}\n\n#kanopi-sidebar #v-image-markup .markup-container .tool-bar .menu {\n  background-color: #313131;\n  border-radius: 4px;\n  display: flex;\n  flex-direction: column;\n}\n\n#kanopi-sidebar #v-image-markup .markup-container .tool-bar .menu button {\n  font-size: 1.5rem;\n  padding: 1rem;\n  color: #fff;\n  transition: all 1s;\n}\n\n#kanopi-sidebar #v-image-markup .markup-container .tool-bar .menu button:hover, #kanopi-sidebar #v-image-markup .markup-container .tool-bar .menu button.active {\n  color: #3E69E0;\n}\n\n#kanopi-sidebar #v-image-markup .markup-container .tool-bar .menu button:focus {\n  outline: none;\n}\n\n#kanopi-sidebar #v-image-markup .markup-container .tool-bar .menu button.button {\n  padding: 1rem;\n  color: #fff;\n}\n\n#kanopi-sidebar #v-image-markup .markup-container .tool-bar .menu button.save {\n  background-color: #3E69E0;\n}\n\n#kanopi-sidebar #v-image-markup .markup-container .tool-bar .menu button.save:hover {\n  color: #313131;\n}\n\n#kanopi-sidebar #v-image-markup .markup-container .tool-bar .menu button.close {\n  background-color: #fff;\n  color: #3E69E0;\n}\n\n#kanopi-sidebar #v-image-markup .markup-container .tool-bar .menu button.close:hover {\n  color: #313131;\n}\n\n#kanopi-sidebar #v-image-markup .markup-container .tool-bar .menu button svg {\n  margin: auto;\n}\n\n#kanopi-sidebar #v-image-markup .markup-container .tool-bar .menu .stroke {\n  padding: 1rem;\n  margin-bottom: 1rem;\n  min-height: 40px;\n}\n\n#kanopi-sidebar #v-image-markup .markup-container .tool-bar .menu .stroke span {\n  display: block;\n  border-bottom-style: solid;\n  border-bottom-color: #000;\n}\n\n#kanopi-sidebar #v-image-markup .markup-container .tool-bar .menu .colour-picker {\n  display: flex;\n}\n\n#kanopi-sidebar #v-image-markup .markup-container .tool-bar .menu .colour-picker input[type=color] {\n  border: 2px solid white;\n  border-radius: 100%;\n  height: 15px;\n  width: 15px;\n  margin: auto;\n  outline: none;\n  padding: 0;\n  -webkit-appearance: none;\n}\n\n#kanopi-sidebar #v-image-markup .markup-container .tool-bar .menu .colour-picker input[type=color]::-webkit-color-swatch-wrapper {\n  padding: 0;\n}\n\n#kanopi-sidebar #v-image-markup .markup-container .tool-bar .menu .colour-picker input[type=color]::-webkit-color-swatch {\n  border: none;\n  border-radius: 100%;\n}\n\n#kanopi-sidebar #v-image-markup .markup-container .tool-bar .menu .stroke-size {\n  padding: 1rem;\n}\n\n#kanopi-sidebar #v-image-markup .markup-container .tool-bar .menu .stroke-size .btns {\n  display: flex;\n  justify-content: space-between;\n}\n\n#kanopi-sidebar #v-image-markup .markup-container .tool-bar .menu .stroke-size .btns button {\n  padding: 0;\n  user-select: none;\n}\n\n#kanopi-sidebar #v-image-markup .markup-container .plans-container {\n  position: absolute;\n  top: 5vh;\n  left: 5vw;\n}\n\n#kanopi-sidebar-container aside.left {\n  left: 15px;\n}\n\naside#kanopi-sidebar {\n  bottom: 20px;\n  display: block;\n  font-family: \"Helvetica\", \"Arial\", \"sans-serif\";\n  left: auto;\n  position: fixed;\n  right: 30px;\n  background-color: transparent;\n  top: auto;\n  width: 60px;\n  